@use '../helpers' as h;
@import '../foundation/base';


.catalog-page-one{
    .wrap-types{
        margin-bottom: 40px;

    }
    .actual-colors-block{
        .h2{
            margin-bottom: 65px;;
        }
        .h4{
            margin-bottom: 20px;
        }
    }
    .price-list-block:before{
        display: none;;
    }
    .actual-colors-block{
        margin-bottom: 100px; ;
    }
}

.catalog-slider{
    overflow: hidden;
    margin-bottom: 112px;
    .slick-dots{
        @include standart-flex;  

        justify-content: center; 
    }
    .wrap-slider-img{
        height: 526px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .slick-next, .slick-prev{
        bottom: 200px;
    }
    .button{
        position: relative;
        top: 0;
        right: 0; 
        width: 255px;
    }
    .wrap-button {
        position: absolute;
        right: 28px;
        top: 15px;
    }
    .slide-price{
        font-weight: 700;
        font-size: 25px;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;
        color: #1A2335;
    }
}

.thumbnails {
    cursor: pointer;
    @include standart-flex; 
    justify-content: center; 
    img{
        max-width: 100px;;
        margin: 10px;;
    }
}


.descriptin-block {
    margin-bottom: 183px;
    @include standart-flex;
    align-items: flex-start;
    .text-block {
        width: 70%;
        padding-left: 20px;
    }
}

.characteristick{
    position: relative;
    padding-top: 250px;;
    margin-bottom: 130px;;
    .h2{
        margin-bottom: 45px;;
    }
}

.char-row {
    @include standart-flex; 
    text-align: left;;
    line-height: 50px;;
    // height: 74px;
    &:nth-child(odd){
        background-color: #F9F9F9;
    }
    .char-left {
        width: 40%;
        padding-left: 15px;
        padding: 33px 15px;
    }
    .char-right{
        width: 40%;
        padding: 33px 15px;
    }
}
.gallery-block{
    margin-bottom: 120px;
}

.parent {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
    height: 780px;
    }
    
    .div1 { grid-area: 1 / 1 / 2 / 5; }
    .div2 { grid-area: 2 / 1 / 3 / 5; }
    .div3 { grid-area: 3 / 1 / 4 / 5; }
    .div4 { grid-area: 1 / 5 / 2 / 9; }
    .div5 { grid-area: 1 / 9 / 2 / 13; }
    .div6 { grid-area: 2 / 5 / 4 / 13; }

    .img-wrp {
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;

        }
        &.img-height {
            height: 235px;
        }

    }

@media screen and (max-width: 1100px){
    .catalog-page-one{
        .slide .button{
            margin-top: 0px;;
        }
        &.catalog-page .h1{
            margin-bottom: 30px;;
        }

    }
    .descriptin-block{
        margin-bottom: 90px;;
    }

}

@media screen and (max-width: 768px){
    .catalog-page-one.catalog-page .h1 {
        margin-bottom: 15px;
    }
    .catalog-page-one{
        .h1{
            @include standart-flex;
            margin-bottom: 15px;
            .slide-price{
                font-weight: 600;
                font-size: 20px;;
            }
        }
        .normal_text{
            margin-bottom: 15px;;
        }
        .catalog-slider .slide-price{
            font-size: 20px;
        }
        // .slide {
        //     padding-top: 30px;
        //     height: 368px;
        // }
        .catalog-slider .wrap-button {
            top: 0px;
        }
        .catalog-slider{
            margin-bottom: 60px;;
        }
        .catalog-slider .slick-next, .catalog-slider .slick-prev {
            bottom: 120px;
        }
        .catalog-slider .wrap-slider-img{
            height: auto;
            img{
                height: 318px;
            }

        }
    }
    .catalog-page-one .catalog-slider .wrap-button {
        top: 0;
        margin-top: 130px;
        right: 0px
    }
    .catalog-page-one{
        .catalog-slider .slick-dots {
            align-items: center;
            display: flex;
            justify-content: space-between;
            justify-content: center;
            position: absolute;
            bottom: 88px;
            left: 0px;
        }
        .catalog-slider .wrap-button {
            position: relative;
        }
    }
    .catalog-page-one .catalog-slider .slick-next, .catalog-page-one .catalog-slider .slick-prev {
        bottom: 208px;
    }
    .descriptin-block{
        flex-direction: column;
        .text-block{
            padding-left: 0px;;
            width: 100%;
        }
    }
    .characteristick{
        padding-top: 118px;
    }
    .catalog-page-one .actual-colors-block{
        margin-bottom: 60px;;
    }
    .img-wrp.img-height{
        height: 135px;
    }
    .parent{
        height: 480px;
    }
    .catalog-page-one .actual-colors-block .h2{
        margin-bottom: 30px;
    }
    .descriptin-block {
        margin-bottom: 60px;
    }
    
}

@media screen and (max-width: 600px){
    .parent {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: repeat(4, 1fr);
        grid-column-gap: 15px;
        grid-row-gap: 15px;
        }
    .div1 { grid-area: 1 / 1 / 2 / 4; }
    .div2 { grid-area: 1 / 4 / 2 / 7; }
    .div3 { grid-area: 2 / 4 / 3 / 7; }
    .div4 { grid-area: 2 / 1 / 3 / 4; }
    .div5 { grid-area: 3 / 1 / 5 / 7; }
    .div6{
        display: none;
    }
    .gallery-block{
        margin-bottom: 60px;
    }
    .characteristick{
        margin-bottom: 60px;;
    }
}


